.mobile {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    .top {
        width: 100%;
        text-align: center;
        margin-top: 20px;
        margin-bottom: 20px;
        img {
            width: 80vw;
        }
    }

    .download {
        text-align: center;
        p {
            height: 22px;
            font-size: 15px;
            font-weight: 500;
            color: rgba(0, 0, 0, 1);
            line-height: 22px;
        }
        a{
            display: block;
            width: 100px;
            margin: 10px auto;
            img{
                width: 100%;
            }
        }
    }
}