

// 全局清除标签样式
body,
html,
ol,
ul,
h1,
h2,
h3,
h4,
h5,
h6,
p,
th,
td,
dl,
dd,
form,
fieldset,
legend,
input,
textarea,
select,
figure,
figcaption {
  margin: 0;
  padding: 0;
}
body,
html {
  height: 100%;
  background-color: #f3f5f7;
  font: 14px "Microsoft YaHei",Helvetica,Arial,'Lucida Grande',Tahoma,sans-serif;
  position: relative;
}
h1,
h2,
h3,
h4,
h5,
h6,
b,
strong {
  font-weight: normal;
  font-size: 100%;
}
i,
em {
  font-style: normal;
}
a {
  text-decoration: none;
  color: #6b6b6b;
}
li {
  list-style: none;
}
input {
  outline: none;
}
button {
  outline: none;
}
p{
  margin-bottom: 0;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  content: " ";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
#root {
  height: 100%;
}
#root ul {
  margin: 0;
}

.treeNodeUnselectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

p{
  margin-bottom: 0;
}




