// @import "~antd/dist/antd.css";
@import "./assets/fonts/iconfont.css";
@import "./mediaqueries.scss";
@import "./theme.scss";

#page {
  width: 100%;
  height: 100%;
  min-width: 768px;
  overflow: hidden;
  position: relative;

  #app-head {
    position: relative;
    height: 70px;
    min-width: 768px;
    background: rgba(255, 255, 255, 1);
    border-bottom: 1px solid rgba(230, 234, 241, 1);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
  }

  header {
    height: 100%;

    .logo {
      float: left;
      position: relative;
      margin-left: 15px;
      margin-right: 15px;
      height: 100%;
      width: 150px;

      h1 {
        position: absolute;
        width: 154px;
        height: 100%;
        top: 0;

        a {
          display: inline-block;
          vertical-align: middle;
          width: 154px;
          height: 30px;
          background: url("./images/logo.png") no-repeat;

          span {
            position: absolute;
            left: -100px;
          }
        }

        &::before {
          content: "";
          display: inline-block;
          height: 100%;
          vertical-align: middle;
        }
      }
    }

    .siderNav {
      float: left;
      height: 100%;
    }

    .buy-btn {
      float: right;
      height: 100%;
      background: $bg-color;

      a {
        display: block;
        padding: 0 20px;
        height: 70px;
        font-size: 14px;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 70px;

        i {
          margin-right: 14px;
        }
      }
    }

    .headerbar {
      float: right;
    }
  }

  .page-content {
    height: 100%;
    padding: 70px 0 0 0;
  }

  .page-content-main {
    height: 100%;
    overflow: hidden;
    background: rgba(243, 245, 247, 1);
  }

  .ant-modal-body{
    padding: 0;
  }

  .icon-xuanzhong1{
    color: $icon;
  }
}

