#app {
  position: relative;
  height: 100%;
  .course {
    position: absolute;
    background-color: #62B1E6;
    top: 70px;
    left: 0;
    right: 0;
    height: 22px;
    line-height: 22px;
    z-index: 99;
    text-align: center;
    .course-inner{
      display: inline-block;
      color: #fff;
      .click{
        color: $bg-color;
        cursor: pointer;
      }
    }
    .close{
     float: right;
     margin-right: 10px;
     cursor: pointer;
    }
  }
}

#message {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  background-color: $bg-color;
  border-radius: 3px;
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
  z-index: 1000;

  span {
    font-size: 14px;
    line-height: 32px;
    padding: 0 20px;
    color: #fff;
  }
}

.module-canvas {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 99;
  text-align: center;

  &::after {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
}

.upphoto-btn,
.cloudAlbumUpload {
  width: 100%;
  position: relative;
  display: inline-block;
  border-radius: 3px;
  overflow: hidden;
  text-indent: 0;
  background-color: #fff;
  text-align: center;
  align-self: center;
  line-height: 42px;
  font-size: 14px;
  font-weight: 500;
  border: 0;
  cursor: pointer;

  input {
    position: absolute;
    height: 42px;
    right: 0;
    top: 0;
    opacity: 0;
  }

  &:hover {
    background-color: $hover;
  }
}



.Tutorial{
  display: flex;
  justify-content: space-around;
  p{
    font-size: 18px;
    margin-bottom: 10px;
  }
}

.icon-check-s {
  position: absolute;
  display: flex;
  align-items: center;
  visibility: hidden;
  top: 5px;
  right: 5px;
  width: 19px;
  height: 19px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  z-index: 1;
  border: 1px solid $icon;
  i{
    font-size: 20px;
    color:$icon;
  }
}